@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,500;0,700;1,300&display=swap);
* {
	margin: 0;
	padding: 0;
	outline: none;
	font-family: 'Roboto', sans-serif;
	-webkit-font-smoothing: antialiased;
}

.flyout {
	display:flex;
	flex-direction: column;
	min-height:100vh;
	justify-content: space-between;
}

.home-feature-box .fa {
	font-size:6rem;
}

.home-feature-box span {
	display: block;
	color:#111;
	font-weight:bold;
	margin-top:1.5rem;
}

.example-components-list li > a {
  color: #495057;
}

.example-components-list li:last-child > a {
	border-bottom:0;
}

.example-components-list li > a .fa {
	color:rgba(0,0,0,.35);
	float:right;
}

/* FOOTER */
.footer-copyright {
	position: absolute;
	bottom: 0;
	width: 100%
}

.footer {
	background: #F7FEFF;
}

